@import url(./style/global_var.css);

:root {
  --commentchatroom-height: 5rem;
  --commentarea-height: 5rem;
  --commentarea-fontsize: 1rem;
  --commentbtn-size: 30px;
}
#Comment_section_container {
  padding-top: calc(
    50vh - (var(--commentchatroom-height) + var(--commentarea-height)) / 2 -
      var(--feedbacktitle-fontsize)
  );
  font-family: var(--primary-font);
}
#Comment_section_chatroom_pc {
  height: var(--commentchatroom-height);

  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;

  transition: all 0.5s;
}

#Comment_section_chatroom_pc:hover {
  border: 2px solid rgba(0, 0, 0, 0.6);
}

#Comment_section_chatroom_mobile {
  position: fixed;
  bottom: calc(2rem + var(--commentarea-height));

  height: var(--commentchatroom-height);

  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;

  transition: all 0.5s;
}

#Comment_section_chatroom_mobile:hover {
  border: 2px solid rgba(0, 0, 0, 0.6);
}

#Comment_section_form_mobile {
  position: fixed !important;
  bottom: 1rem;
}

#Comment_section_textarea_pc {
  height: var(--commentarea-height);

  border-radius: 10px;
  border: 2px solid black;

  /* background-color: transparent; */

  font-size: var(--commentarea-fontsize);
  font-family: var(--primary-font);

  resize: none;
}

#Comment_section_textarea_mobile {
  height: var(--commentarea-height);

  border-radius: 10px;
  border: 2px solid black;

  /* background-color: transparent; */

  font-size: var(--commentarea-fontsize);
  font-family: var(--primary-font);

  resize: none;
}

#Comment_section_send_icon {
  width: var(--commentbtn-size);
  height: var(--commentbtn-size);
}

#Comment_section_submit_btn {
  height: calc(var(--commentbtn-size) * 1.2);
  width: calc(var(--commentbtn-size) * 1.2);

  border-radius: 50%;
}

@media screen and (min-width: 300px) {
  :root {
    /* --commentchatroom-height: 25rem; */
    --commentarea-height: 3rem;
    --commentbtn-size: 35px;
    --commentarea-fontsize: 1rem;
  }
}
@media screen and (min-width: 350px) {
  :root {
    /* --commentchatroom-height: 25rem; */
    --commentarea-height: 3rem;
    --commentbtn-size: 35px;
  }
}
