}

.page_container {
  background-image: url(/static/media/background_pc.676b0016.png);
  background-image: var(--bg-image-pc);

  background-size: calc(1440px * var(--background-size))
    calc(810px * var(--background-size));

  background-position: calc(
      (100vw - 100px - 1440px * var(--background-size)) / 2
    ),
    calc((100vh - 810px * var(--background-size)) / 2);

  background-repeat: no-repeat;

  font-family: "Noto Sans TC", Arial, sans-serif;

  font-family: var(--primary-font);
}
