.Login_div {
  margin-top: var(--login-margintop);
}
.separator {
  display: block;

  height: 2px;
  width: 100%;

  background: #68b3ff;

  transition: all 0.25s linear;

  transform: scaleX(0) translateY(-2px);
  transform-origin: 50%;

  opacity: 0;
}

.Login_input:focus + .separator {
  transform: scaleX(1) translateY(-2px);
  opacity: 1;
}

.Login_input:hover + .separator {
  transform: scaleX(1) translateY(-2px);
  opacity: 1;
}

.Login_input {
  border: 0;

  background-color: transparent;

  font-size: var(--login-fontsize);
  font-family: var(--primary-font);
}

.Login_div > div > label {
  font-size: var(--login-fontsize);
}

#Login_container {
  width: calc(100vw - var(--navbar-width));
  height: 100vh;

  padding-top: calc(50vh - var(--login-height) / 2);

  /* background-image: var(--bg-image); */

  /* font-family:var(--primary-font); */
}
#Login_text {
  font-size: var(--logintitle-fontsize);
  text-align: center;
}
#Login_input {
  height: var(--login-height);

  border: 3px whitesmoke solid;
  border-radius: 20px;

  background-color: rgba(255, 255, 255, 0.6);
}

#Login_submit {
  height: var(--loginbtn-height);

  border: 2px solid;
  border-color: rgba(0, 0, 0, 1);
  border-radius: 10px;
  background-color: transparent;

  font-size: var(--loginbtn-fontsize);
  color: black;

  transition: all 0.3s;
}

#Login_submit:hover {
  background-image: linear-gradient(
    150deg,
    #ffb8ba,
    #ffb8ba 10%,
    #e8b77b 20%,
    #e8b77b 30%,
    #fffa94 40%,
    #fffa94 50%,
    #7bebba 60%,
    #7bebba 70%,
    #9ceaff 80%,
    #9ceaff 100%
  );
  color: black;
}

