  font-family: var(--primary-font);

  transition: all 0.5s;
}

#Home_anime_container {
  position: relative;

  width: calc(320px * var(--size-adjust));

  width: calc(var(--anime-width) * var(--size-adjust));
  height: calc(320px * var(--size-adjust));
  height: calc(var(--anime-height) * var(--size-adjust));
}

#Home_book {
  position: absolute;
  top: calc(50% - var(--size-adjust) * 234px / 2);
  left: calc(50% - var(--size-adjust) * 192px / 2);

  height: calc(var(--size-adjust) * 234px);
  width: calc(var(--size-adjust) * 192px);

  transition: all 0.5s;
  -webkit-animation: opening_anime 2s 2s 1 normal forwards;
          animation: opening_anime 2s 2s 1 normal forwards;

  opacity: 0;
}

#Home_left_hand {
  position: absolute;

  height: calc(var(--size-adjust) * 84px);
