  #Home_btns button > img {
    height: calc((var(--size-adjust) - 0.7) * 3rem);
    width: calc((var(--size-adjust) - 0.7) * 3rem);
  }

  #Home_social_btn a > button > img {
    height: calc((var(--size-adjust) - 0.8) * 3rem);
    width: calc((var(--size-adjust) - 0.8) * 3rem);
  }

  #Home_social_btn button {
    height: calc((var(--size-adjust) - 0.8) * 5rem);
    width: calc((var(--size-adjust) - 0.8) * 5rem);
  }
}
@media screen and (min-width: 767px) and (min-height: 800px) {
  :root {
    --size-adjust: 1.75;
  }

  #Home_btns p {
    margin-top: 0.5rem;

    font-size: 1rem;
  }

  #Home_social_btn {
    position: absolute;
    bottom: 1%;
    right: 5%;
  }
}
@media screen and (min-width: 1023px) and (min-height: 850px) {
  :root {
    --size-adjust: 2;
  }

  #Home_btns p {
    margin-top: 0.7rem;

    font-size: 1.2rem;
  }
}
